import Cover from '../../graphics/imageCover/Cover'
import Text from '../../graphics/textDisplayer/Text'

export default function AlphaDisplayer({children, style, height, width, image, title, description, buttonText, clickAction}) {
    
    const textStyle = {
        titleStyle : "",
        descriptionStyle : "",
        buttonStyle : "",
    }
    
    return (
        <div className={style}>
            <Cover image={image} height={height} style={`flex flex-col justify-end items-center ${height} ${width} shadow-lg`}>
                <Text text={title} style="m-2" textStyle="text-white text-xl font-bold"/>
                <Text text={description} style="m-1" textStyle="text-white text-center"/>
                <button className="h-10 w-40 m-3 bg-white text-gray-900">{buttonText}</button>
            </Cover>
            {children}
        </div>
    )
} 